import React from 'react'
import { Link } from 'react-router-dom'
import MoreVertIcon from '@mui/icons-material/MoreVert';
import AccountCircleOutlinedIcon from '@mui/icons-material/AccountCircleOutlined';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import '../styles/style.css'
import { Search } from '@mui/icons-material';
const Navbar = () => {
  let cart = document.getElementById("cart")
  let cartDetails = document.getElementById("cart-details")
  if(cart!=null){
    cart.addEventListener("mouseover", ()=>{
      cartDetails.setAttribute("style","color: black;background-color: white;min-height: 200px;width: 210px;position: absolute;top: 45px;right:100px;display: flex;flex-direction: column;justify-content: space-evenly;border-radius: 10px;")
    })
    cart.addEventListener("mouseout", ()=>{
      cartDetails.setAttribute("style","display:none")
    })
  }
  return (
    <div className="navbar">
      <div className="left">
        <h1>BRAND</h1>
        <Search id="searchicon"/>
        <input type="text" id='search' placeholder='Search for Products here' />
      </div>
      <div className="right">
        <Link to={"/login"} ><AccountCircleOutlinedIcon />Login</Link>
        {/* <div id="login-dropdown">
        </div> */}
        <div id="cart">
        <Link to={"/cart"} ><ShoppingCartOutlinedIcon /> Cart</Link>
        <div id="cart-details">
          <p>No order has placed</p>
        </div>
        </div>
        <MoreVertIcon />
      </div>
    </div>
  )
}

export default Navbar