import React, { useEffect, useState } from 'react'
import Navbar from './Navbar'
import { Link } from 'react-router-dom'
import {getDatabase, ref, get } from 'firebase/database'
import config from './Config'
import NavigateNextRoundedIcon from '@mui/icons-material/NavigateNextRounded';
import LinearProgress from '@mui/material/LinearProgress';

const Home = () => {
  let [products, setProducts]=useState([])
  let [categories, setCategories]=useState([])
  let [electronics, setElectronics]=useState([])
  let [furnitures, setFurnitures]=useState([])
  let [fashion, setFashion]=useState([])
  let [mens,setMens]=useState([])
  let [womens,setWomens]=useState([])
  let [kids, setKids]=useState([])
  let [books,setBooks]=useState([])

  const fetchData = async ()=>{
    let db = getDatabase(config)
    let dbRef= ref(db, "/")
    let snapshots = await get(dbRef)
    if(snapshots.exists()){
      setProducts(Object.values(snapshots.val()))
    }
    else{
      alert("Error")
    }
  }
  let showMenu = ()=>{
    let category = document.getElementsByClassName("category")
    let menu = document.getElementsByClassName("categ-menu")
    category[0].addEventListener("mouseover",()=>{
      menu[0].setAttribute("style","display:block")
    })
    category[0].addEventListener("mouseout",()=>{
      menu[0].setAttribute("style","display:none")
    })
    category[1].addEventListener("mouseover",()=>{
      menu[1].setAttribute("style","display:block")
    })
    category[1].addEventListener("mouseout",()=>{
      menu[1].setAttribute("style","display:none")
    })
    category[2].addEventListener("mouseover",()=>{
      menu[2].setAttribute("style","display:block")
    })
    category[2].addEventListener("mouseout",()=>{
      menu[2].setAttribute("style","display:none")
    })
    category[3].addEventListener("mouseover",()=>{
      menu[3].setAttribute("style","display:block")
    })
    category[3].addEventListener("mouseout",()=>{
      menu[3].setAttribute("style","display:none")
    })
    category[4].addEventListener("mouseover",()=>{
      menu[4].setAttribute("style","display:block")
    })
    category[4].addEventListener("mouseout",()=>{
      menu[4].setAttribute("style","display:none")
    })
  }
  useEffect(()=>{
    fetchData()
    if(products.length>0){
      setCategories(Object.keys(products[0]))
      setElectronics(products[0]["electronics"]["desktops"])
      setFurnitures(products[0]["furnitures"]["items"])
      setFashion(products[0]["fashion"])
      setMens(fashion["mens"])
      setWomens(fashion["women"])
      setKids(fashion["kids"])
      setBooks(products[0]['books']["items"])
    }
    if(mens!=null && mens.length>0){
      showMenu()
      setMens(mens.slice(0,4))
      setWomens(womens.slice(0,4))
      setKids(kids.slice(0,4))
    }
    console.log(books)
  },[products])
  if(products.length>0 && mens!=null){
    return (
      <div className="home">
        <Navbar />
        {/* Categories Part */}
        <div className="categories">
          {categories.map((x) => {
            return <div className="category">
              <Link to={"/"}>
                <img src={products[0][x]["img"]} alt="" />
                <br />
                <p>{x}</p>
                <div className='categ-menu'>
                  {Object.keys(products[0][x]).filter((x)=>{
                    return x!="img" && x!="items"
                  }).map((x)=>{
                    return <div>
                      <Link to="/">{x}</Link>
                    </div>
                  })
                  }
                </div>
              </Link>
            </div>
          })}
        </div>
        <div className="section1">
          <div className="head">
          <h2>Top Deals on Electronics</h2>
          <Link to={"/"}><NavigateNextRoundedIcon id="see-all"/></Link>
          </div>
          <div className="items">
            {electronics.map((x) => {
              return <div className="item">
                <img src={x.img} alt="" />
                <br />
                <br />
                <Link to="/">{x.name}</Link>
                <p>{x.price}</p>
              </div>
            })}
          </div>
        </div>
        <div className="section1">
        <div className="head">
          <h2>Buy Amazing Furniures</h2>
          <Link to={"/"}><NavigateNextRoundedIcon id="see-all"/></Link>
          </div>
          <div className="items">
            {furnitures.map((x) => {
              return <div className="item">
                <img src={x.img} alt="" />
                <br />
                <br />
                <Link to="/">{x.name}</Link>
                <p>{x.price}</p>
              </div>
            })}
          </div>
        </div>
        <div className="head" id='fashion-head'>
          <h2>Fashion New Deals</h2>
          <Link to={"/"}><NavigateNextRoundedIcon id="see-all"/></Link>
          </div>
        <div className="section2">
          <div className="sect">
            <h3>Men's</h3>
            <div className='items'>
            {mens.map((x)=>{
              return <div className="item">
              <img src={x.img} alt="" />
              <br />
              <Link to="/">{x.name}</Link>
              <p>{x.price}</p>
            </div>
            })}
            </div>
          </div>
          <div className="sect">
            <h3>Kids</h3>
            <div className='items'>
            {kids.map((x)=>{
              return <div className="item">
              <img src={x.img} alt="" />
              <br />
              <Link to="/">{x.name}</Link>
              <p>{x.price}</p>
            </div>
            })}
            </div>
          </div>
          <div className="sect">
            <h3>Women's</h3>
            <div className='items'>
            {womens.map((x)=>{
              return <div className="item">
              <img src={x.img} alt="" />
              <br />
              <Link to="/">{x.name}</Link>
              <p>{x.price}</p>
            </div>
            })}
            </div>
          </div>
        </div>
        <div className="section1">
        <div className="head">
          <h2>Books</h2>
          <Link to={"/"}><NavigateNextRoundedIcon id="see-all"/></Link>
          </div>
          <div className="items">
            {books.map((x) => {
              return <div className="item">
                <img src={x.img} alt="" />
                <br />
                <br />
                <Link to="/">{x.name}</Link>
                <p>{x.price}</p>
              </div>
            })}
          </div>
        </div>
      </div>
    )
  }
  else{
    return <div>
      <Navbar />
      <LinearProgress/>
    </div>
  }
}

export default Home